<template>
  <div class="wrapper">
      <el-container>
		
		
		<!-- <swriper1></swriper1> -->
		<!-- <img src="@/assets/image/home_img_banner.png" alt=""> -->
		<div class="banner"></div>
		<yewu></yewu>
		<ck></ck>
		<hj></hj>
		<el-main>
				
			<font1 :Font="Font1"></font1>
			<button style="color: #FFFFFF; width: 100px;height: 30px;border-radius: 5px;background: #007BFF; cursor: pointer;" @click="show = !show">
				{{show?'':''}}
			</button>
			<transition name="slide-fade">
				<show :rentCarInfo="rentCarInfo" v-if="show"></show>
			</transition>
			<div style="height: 20px;"></div>
			
			<!-- <show :rentCar="rentCar"></show> -->
			<div class="bg-gray bounceInLeft wow" data-wow-delay="0.5s">
				<font1 :Font="Font2"></font1>
				<texts :text="text1"></texts>
				<div style="height: 20px;"></div>
				<!--  -->
				<Load v-if="!play1"></Load>
				<showPlay :play="play1" v-if="play1"></showPlay>
				<div style="height: 20px;"></div>
				<pages 
					@current="current"
					:tatol="tatol"
				></pages>
			</div>
			<div class="bg-white">
				<font1 :Font="Font3"></font1>
				
				<el-row :gutter="10">
				 
				  <el-col :xs="24" :sm="17" :md="17" :lg="17" :xl="17">
					  <div style="letter-spacing:6px;text-indent:2em">
						30 .
					  </div>
					  
					  <div style="letter-spacing:6px;text-indent:2em;margin-top:15px">
						  
					  </div>
				  </el-col>
				  <el-col :xs="24" :sm="7" :md="7" :lg="7" :xl="7">
					  <div>
						  <img src="@/assets/image/gs.png" alt="" style="width: 320px;height: 300px;">
					  </div>
				 </el-col> 
				</el-row>
			</div>
		
		</el-main>
		<div style="height: 20px;"></div>
		<cn></cn>
		<el-footer>
			<div style="height: 20px;"></div>
			<footers></footers>
		</el-footer>
      </el-container>
  </div>
</template>

<script>
	import Nav1 from "@/components/nav.vue"
	import swriper1 from "@/components/swriper.vue"
	import font1 from "@/components/font.vue"
	import show from "@/components/show.vue"
	import downMenu from "@/components/downMenu.vue"
	import texts from "@/components/texts.vue"
	import showPlay from "@/components/showPlay.vue"
	import pages from "@/components/pages.vue"
	import footers from "@/components/footers.vue"
	import cn from "@/components/cn.vue"
	import Load from "@/components/load.vue"
	import yewu from "@/components/yewu.vue"
	import ck from "@/components/ck.vue"
	import hj from "@/components/hj.vue"
	
	import {WOW} from '@/assets/js/wow.min.js' 
	export default {
		async mounted(){
			this.queryPageSize()
			
			var wow = new WOW({
			    boxClass: 'wow',
			    animateClass: 'animated',
			    offset: 0,
			    mobile: true,
			    live: true
			});
			new WOW().init();
			
		},
		beforeCreate() {
			this.$A.get('/api/querytArticle')
			.then(res =>{
				let data = res.data.data.length
				this.tatol = Math.ceil(data/this.pageSize)
			})
		},
		components: {
			Nav1,
			swriper1,
			font1,
			show,
			downMenu,
			texts,
			showPlay,
			pages,
			footers,
			cn,
			Load,
			yewu,
			ck,
			hj
		},
		data() {
			return {
				base:"",
				page:0,
				pageSize:4,
				tatol:9,
				obj1:[],
				show: true,
				rentCarInfo:[],
				Font1:{
						one:"Freedom to walk",
						two:"MODEL SELECTION",
						three:""
				},
				Font2:{
						one:"Personalized service",
						two:"OUR SERVICES",
						three:"--   "
				},
				Font3:{
						one:"Our advantages",
						two:"WHY CHOOSE US",
						three:""
				},
				Font4:{
						one:"Tourist attractions",
						two:"TRAVEL GUIDES",
						three:"-"
				},
				
				text1:[
					{
						title:"",
						content:""
					},
					{
						title:"",
						content:""
					},
					{
						title:"",
						content:"180"
					},
					{
						title:"",
						content:""
					}
				],
				play1:""
					 
			}
		},
		beforeCreate:async function(){
			this.$A.get("/api/queryPrice")
			.then(res =>{
				const data = res.data.data
				this.rentCarInfo = data
			})
		},
		
		methods: {
			
		   getExecStrs(str) {
		          var reg = /<img src="(.+?)\">/g
		          var list = []
		          var result = null
		          do {
		              result = reg.exec(str)
		              result && list.push(result[1])
		          } while (result)
				  if(list.length<=0){
					  return;
				  }
		          return list
		   },
		   async queryPageSize(){
			   await this.$A.get('/api/queryArticleByPage',{params: {page: this.page*this.pageSize,pageSize: this.pageSize}})
			      .then(res =>{
			   		this.obj1 = res.data.data
			      })
			   this.getplay()
		   },
		   getNo(str){
			   var reg = /<img src="(.+?)\">/g
			       // var reg = /\$\{(.+?)\}/g
			   var list = str.replace(reg,'')
			   return list
		   },
		   setSize(str){
			   var reg = /<img /g
			       // var reg = /\$\{(.+?)\}/g
			   var list = str.replace(reg,"<img style='width:484px;height:300px'")
			   return list
		   },
		   getplay(){
			   let news = this.obj1.map((r,i) =>{
				   return{
					   id:r.id,
					   url:this.getExecStrs(r.content)==undefined?' ':this.getExecStrs(r.content)[0] ,
					   title:r.title,
					   content:r.content,
					   time:new Date(parseInt(r.time)).toLocaleString()
				   }
			   })
			   this.play1 = news
			   // console.log(this.play1 )
			   
		   },
		   current(e){
			   // console.log(e)
			   if(e == 1){
				   if(e-1 == this.page){
						return;
				   }
				   
				   this.page = e-1
				   this.queryPageSize()
				   
			   }else{
				   if(e-1 == this.page){
					   return;
				   }
				   this.page = e-1
				   this.queryPageSize()
			   }
		   },
			
		}
	}
</script>

<style>
	
	
	.el-main{
		overflow: hidden !important;
		padding: 0px !important;
		position: relative;
	}
	.grid-content{
		width: 50px;
		height: 30px;
	}
	.im{
		width: 100%;
		height: 100%;
		padding: 10px;
	}
	@media screen and (min-width: 768px) {
	    .im{
	        width: 100px;
			height: 40px;
	    }

	}

	/*  */
	/*  */
	.slide-fade-enter-active {
	  transition: all .3s ease;
	}
	.slide-fade-leave-active {
	  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
	}
	.slide-fade-enter, .slide-fade-leave-to
	/* .slide-fade-leave-active for below version 2.1.8 */ {
	  transform: translateX(10px);
	  opacity: 0;
	}
	/* .el-main{
		padding: 0px !important;
	} */
	
	.banner{
		width: 100%;
		height: 0;
		padding-bottom: 53.33%;
		background: url("../../assets/image/home_img_banner.png") no-repeat;
		background-size: 100% 100%;
		/* position: relative; */
		/* z-index: 1; */
	}
	.el-footer{
		padding: 0px !important;
	}
</style>