<template>
	<div v-if="show" @click="toTop()" class="flex flex-column" style=";position: fixed;right: 50px;bottom: 100px;z-index:899">
		<img src="@/assets/image/huojian.png" style="width: 60px;height: 100px;" alt="">
		<span style="font-size: 18px;"></span>
	</div>
</template>

<script>
	export default{
		data() {
			return {
				show: false,
				// move:false
			}
		},
		
		mounted() {
			this.listenerFunction();
		},
		methods: {
			listenerFunction(e) {
			    document.addEventListener('scroll', this.handleScroll, true);
			},
			handleScroll () {
			    if(window.pageYOffset>340){
					this.show =true
				}else{
					this.show =false
				}
			},
			toTop() {
			  // this.move = true
			  let top = document.documentElement.scrollTop || document.body.scrollTop;
			  //  
			  const timeTop = setInterval(() => {
				document.body.scrollTop = document.documentElement.scrollTop = top -= 50;
				if (top <= 0) {
				  clearInterval(timeTop);
				}
			  }, 10);
			}
		},
		
	}
</script>

<style>
	/* .move{
		transform: translateY(-200px);
		transition:all .1s ease-in .1s;
	} */
</style>
