import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/index/index.vue'

Vue.use(VueRouter)

const routes = [
	{
	    path: '/',
	    name: 'Index',
	    component: Index,
		meta:{
		  keepAlive:true,
		}
	},
	{
	    path: '/resume',
	    name: 'Resume',
	    component: () => import('@/views/resume/resume.vue'),
		meta:{
		  keepAlive:true,
		}
	},
	

	
	{
	    path: '/prices',
	    name: 'Prices',
	    component: () => import('@/views/prices/prices.vue'),
		meta:{
		  keepAlive:false,
		}
	},
	{
	    path: '/service',
	    name: 'Service',
	    component: () => import('@/views/service/service.vue'),
		meta:{
		  keepAlive:true,
		}
	},
	{
	    path: '/callMe',
	    name: 'CallMe',
	    component: () => import('@/views/callMe/callMe.vue'),
		meta:{
		  keepAlive:true,
		}
	},
	{
	    path: '/detail',
	    name: 'Detail',
	    component: () => import('@/views/detail/detail.vue'),
		meta:{
		  keepAlive:false,
		},
		children:[
			{
			    path: 'detailInfo',
			    name: 'DetailInfo',
			    component: () => import('@/components/detailInfo.vue'),
				meta:{
				  keepAlive:false,
				}
			}
		]
	},
	{
	    path: '/scenery',
	    name: 'Scenery',
	    component: () => import('@/views/scenery/scenery.vue'),
		meta:{
		  keepAlive:true,
		}
	},
	{
	    path: '/rentalCar',
	    name: 'RentalCar',
	    component: () => import('@/views/rentalCar/rentalCar.vue'),
		meta:{
		  keepAlive:true,
		}
	},
	{
	    path: '/kt',
	    name: 'Kt',
	    component: () => import('@/views/kt/kt.vue'),
		meta:{
		  keepAlive:true,
		}
	},
	
	
	
	
	
	{
	    path: '/management',
	    name: 'Management',
	    component: () => import('@/views/management/management.vue'),
		redirect:{
			name:'Messages'
		},
		children:[
			{
			    path: 'messages',
			    name: 'Messages',
			    component: () => import('@/components/messages.vue'),
			},
			{
			    path: 'editor',
			    name: 'Editor',
			    component: () => import('@/components/editor.vue'),
			},
			{
			    path: 'wz',
			    name: 'Wz',
			    component: () => import('@/components/wz.vue'),
			},
			{
			    path: 'addprice',
			    name: 'addprice',
			    component: () => import('@/components/backEnd/addPrice.vue'),
			},
			{
			    path: 'queryCarInfo',
			    name: 'queryCarInfo',
			    component: () => import('@/components/backEnd/queryCarInfo.vue'),
			}
		]
	},
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

