import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
	  showHead:true,
	  storeCot:[]
  },
  mutations: {
	  show(state,e){
		  if(e === '/management'||e === '/management/editor'||e === '/management/messages'||e === '/management/wz'||e === '/management/addprice'||e === '/management/queryCarInfo'){
			 state.showHead = false
		  }
	  },
	  changeDetail(state,e){
		state.storeCot = e;
	  }
  },
  actions: {
  },
  modules: {
  }
})
