<script>
	import Nav1 from "./nav.vue"
	import downMenu from "./downMenu.vue"
	export default {
		components: {
			Nav1,
			downMenu
		},
		data() {
			return {
				hd:false,
				bgc:false
			}
		},
		mounted() {
			this.listenerFunction();
			 
			
		},
		watch:{
		  $route(to,from){
			  console.log(to.path)
		    if(to.path != '/'){
				this.bgc = true
			}else{
				this.bgc = false
			}
		  }
		},
		methods: {
			
			listenerFunction(e) {
			    document.addEventListener('scroll', this.handleScroll, true);
			},
			handleScroll () {
			    if(window.pageYOffset>240){
					this.hd =true
				}else{
					this.hd =false
				}
			},
		},
	}
</script>

